    border:  none;
    font-size: calc(12px,2vw,24px);
    color: #fff;
}
@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); background-color: #007bff; }
  100% { transform: scale(1); }
}

.StartAnimation:active{
    animation: pulse 0.3s ease;
}